.login-page {
  height: 100vh !important;
  width: 99vw !important;
  overflow: auto;
  display: flex;
  margin: auto;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to right, #ffc8c6, #fbd786, #5a58e9);
  /* box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px; */
}
.login-page h1 {
  margin-top: 10px;
  text-align: center;
  font-weight: bold;
  color: #050505;
}
.login-form {
  background-color: #b9bcccb6;
  margin-left: -10px;
  padding: 30px;
}

.login-form h1 {
  text-align: center;
  font-weight: bold;
  margin: 30px;
  color: #344265;
}

.login-form .ant-form-item-label label {
  color: #344265 !important;
}
.login-form button {
  background-color: #344265;
  color: white;
  outline: none;
}
.login-form button:hover {
  background-color: black;
  color: white;
}
.login-form a {
  color: #344265 !important;
}

.login-form a:hover {
  color: black !important;
}
