* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
}

.navbar {
  background-color: rgb(114, 112, 110) !important;
}

.content {
  height: 100vh;
}

.register_page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* TRANSACTION PAGE*/
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

/* TRANSACTION PAGE ENDS*/

/* antd ICONS */
.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}

.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.685);
  border-radius: 5px;
  padding: 10px 15px;
}
/* antd ICONS ENDS HERE*/
